<template>
  <div class="area" ref="wrapper">
    <div class="wrapper">
      <div class="item">
        <div class="title">您的位置</div>
        <div class="city-list">
          <div class="city-item">
            <div class="item-con">{{this.city}}</div>
          </div>
        </div>
      </div>
      <div class="item">
        <div class="title">热门城市</div>
        <div class="city-list">
          <div
          v-for="hotItem of list"
          :key="hotItem.id"
          @click="handleCityClick(hotItem.name)"
          class="city-item">
            <div class="item-con ellipsis">{{hotItem.name}}</div>
          </div>
        </div>
      </div>
      <div
      class="item"
      v-for="(cityItem, word) in cities"
      :key="word"
      :ref="word">
        <div class="title">{{word}}</div>
        <div class="all-city-list">
          <div
          class="all-city-item ellipsis"
          v-for="city of cityItem"
          :key="city.id"
          @click="handleCityClick(city.name)">
            {{city.name}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
import {mapState, mapMutations} from 'vuex'
export default {
  data () {
    return {}
  },
  props: {
    list: Array,
    cities: Object,
    keyword: String
  },
  computed: {
    ...mapState(['city'])
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.wrapper, {click: true, taps: true})
  },
  watch: {
    keyword () {
      this.scroll.scrollToElement(this.$refs[this.keyword][0])
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  }
}
</script>
<style lang="scss" scoped>
  .area {
    position: absolute;
    top: 160px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    .item {
      .title {
        padding-left: 20px;
        background: #f9f6f6;
        height: 60px;
        line-height: 60px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;
      }
      .all-city-list{
        .all-city-item {
          width:100%;
          height:60px;
          line-height: 60px;
          padding-left: 20px;
          border-bottom: 1px solid #ccc;
        }
      }
      .city-list{
        padding: 15px 40px 15px 20px;
        display: flex;
        flex-wrap: wrap;
        .city-item{
          width: 33.33%;
          padding: 10px;
          .item-con{
           text-align: center;
           border: 1px solid #00bcd4;
           border-radius: 5px;
           padding: 20px 0;
          }
        }
      }
    }
  }
</style>



